// const Login = resolve => { require(['@/pages/login'], resolve); }
// const Main = () => import('@/pages/main')
// 根据环境的不同切换路由懒加载
const _import = require('./_import_' + process.env.NODE_ENV)
const Main = _import('main/main')

const loginRouter = {
	path: '/login',
	name: 'Login',
	meta: {
		title: 'Login -登录'
	},
	component: _import('login/index')
}

export const page404 = {
  path: '/*',     //  匹配所有页面，找不到存在的路由则跳到404
  name: 'error-404',
  meta: {
    title: '404-页面不存在'
  },
  component: _import('error-page/404')
};

// 作为Main组件的子页面展示但是不在左侧菜单显示的路由写在otherRouter里
export const otherRouter = {
  path: '/',
  // name: 'otherRouter',
  icon: 'logo-buffer',
  title: '控制台',
  component: Main,  // 主组件
  redirect: '/home',  // 设置空url跳转到home页面
  children: [  // 子路由，显示在主组件的<router-view></router-view>里
    { path: 'home', title:'控制台', name: 'home_index', component: _import('home/home')} // , title: {i18n: 'home'}
  ]
}

export const articleRouter = {
  path: '/article',
  icon: 'ios-folder',
  name: 'article',
  title: '文章管理',
  component: Main,
  children: [
    {
      path: 'list',
      icon: 'ios-list-box',
      name: 'list',
      title: '文章列表',
      component: _import('article/list/list')
    },{
      path: 'release',
      icon: 'ios-paper',
      name: 'release',
      title: '发布文章',
      component: _import('article/release/release')
    }
  ]
}

// 测试路由
const groupRouter = {
  path: '/group',
  icon: 'ios-folder',
  name: 'group',
  title: 'test',
  component: Main,
  children: [
    {
      path: 'page1',
      icon: 'ios-paper-outline',
      name: 'page1',
      title: '发布文章',
      component: _import('group/page1/page1')
    },{
      path: 'page2',
      icon: 'ios-list-outline',
      name: 'page2',
      title: '文章列表',
      component: _import('group/page2/page2')
    }
  ]
}

// 作为Main组件的子页面展示并且在点击左侧菜单后显示的路由写在appRouter里
export const appRouter = [
  otherRouter,
  articleRouter,
  // groupRouter,
  {
    path: '/tag',
    icon: 'ios-pricetags',
    name: 'tag',
    title: '标签管理',
    component: Main,
    children: [
      // 子路由path无需加/
      { path: 'index', title: 'Tag', name: 'tag_index', component: _import('tag/tag')}
    ]
  },{
    path: '/link',
    icon: 'ios-link',
    name: 'link',
    title: '链接管理',
    component: Main,
    children: [
      { path: 'index', title: 'Link', name: 'link_index', component: _import('link/link')}
    ]
  },{
    path: '/user',
    icon: 'ios-people',
    name: 'user',
    title: '用户管理',
    component: Main,
    children: [
      { path: 'index', title: 'User', name: 'user_index', component: _import('user/user')}
    ]
  }
];

export const routers = [
  loginRouter,
  otherRouter,
  ...appRouter,
  page404
]


